<template>
  <div class="oneoutbox">
    <div class="selectoutbox">
      <el-select v-model="value" placeholder="请选择" @change="selectChange(value)">
        <el-option
          v-for="item in options"
          :key="item.value"
          :label="item.label"
          :value="item.value"
        ></el-option>
      </el-select>
    </div>
    <div class="timeinputoutbox">
      <div class="mntimefont">模拟时间:</div>
      <div>
        <el-input v-model="inputStarttime" placeholder="请输入内容" type="time"></el-input>
      </div>
      <div class="rowline">—</div>
      <div>
        <el-input v-model="inputEndtime" placeholder="请输入内容" type="time"></el-input>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  name: "oneindex",
  data() {
    return {
      options: [
        {
          value: "0",
          label: "模拟调度"
        },
        {
          value: "1",
          label: "智能调度"
        }
      ],
      value: "0",
      inputStarttime: "00:00",
      inputEndtime: "23:59"
    };
  },
  methods: {
    selectChange(value) {
      this.$store.commit("dispatchType", value);
    }
  }
};
</script>
<style lang="scss" scoped>
@import "@/styles/global.scss";
.oneoutbox {
  display: flex;
  flex-flow: row nowrap;
  justify-content: flex-start;
  align-items: center;
  width: vw(1138);
  height: vh(60);
  .selectoutbox {
    display: flex;
    flex-flow: row nowrap;
    justify-content: flex-start;
    align-items: center;
    width: vw(120);
    margin-left: vw(20);
    /deep/ .el-input__inner {
      height: vh(28);
      line-height: vh(28);
    }
    /deep/ .el-input__icon {
      line-height: vh(28);
    }
  }
  .timeinputoutbox {
    display: flex;
    flex-flow: row nowrap;
    justify-content: flex-start;
    align-items: center;
    font: normal vh(12) "微软雅黑";
    margin-left: vw(30);
    .mntimefont {
      display: flex;
      flex-flow: row nowrap;
      justify-content: flex-start;
      align-items: center;
      margin-right: vw(10);
    }
    .rowline {
      margin-left: vw(5);
      margin-right: vw(5);
    }
    /deep/ .el-input__inner {
      height: vh(28);
      line-height: vh(28);
    }
    /deep/ .el-input__icon {
      line-height: vh(28);
    }
  }
}
</style>



