<template>
  <div class="left" v-show="closeopenstatus">
    <div class="one">
      <oneindex />
    </div>
    <div class="two">
      <twoindex />
    </div>
    <div class="three">
      <threeindex />
    </div>
    <div class="four">
      <fourindex/>
    </div>
    <div class="five">
      <fiveindex/>
    </div>
  </div>
</template>
<script>
import { mapState } from "vuex";
import oneindex from "./one/index";
import twoindex from "./two/index";
import threeindex from "./three/index";
import fourindex from './four/index'
import fiveindex from './five/index'
export default {
  name: "left",
  components: {
    oneindex,
    twoindex,
    threeindex,
    fourindex,
    fiveindex
  },
  computed: {
    ...mapState(["closeopenstatus"])
  }
};
</script>
<style lang="scss" scoped>
@import "@/styles/global.scss";
.left {
  display: flex;
  flex-flow: column;
  justify-content: flex-start;
  align-items: flex-start;
  width: vw(1138);
  height: vh(1039);
  background-color: rgb(240, 240, 240);
  .one {
    display: flex;
    flex-flow: row nowrap;
    justify-content: flex-start;
    align-items: center;
    width: vw(1138);
    height: vh(60);
  }
  .two {
    display: flex;
    flex-flow: column;
    justify-content: flex-start;
    align-items: flex-start;
    width: vw(1138);
    height: vh(359);
  }
  .three {
    display: flex;
    flex-flow: column;
    justify-content: flex-start;
    align-items: flex-start;
    width: vw(1138);
    height: vh(300);
  }
  .four {
    display: flex;
    flex-flow: column;
    justify-content: flex-start;
    align-items: flex-start;
    width: vw(1138);
    height: vh(250);
  }
  .five {
    display: flex;
    flex-flow: row nowrap;
    justify-content: flex-start;
    align-items: center;
    width: vw(1138);
    height: vh(70);

  }
}
</style>



