<template>
  <div ref="g2box" class="g2boxStyle"></div>
</template>

<script>
import g2 from "@antv/g2";
export default {
  name: "g2",
  props: {
    g2data: {
      type: Array
    }
  },
  created() {
    //不能在created方法中初始化
    //this.initg2()
  },
  mounted() {
    this.initg2();
  },
  methods: {
    initg2() {
      const offsetHeight = this.$refs.g2box.offsetHeight; // 获取组件高度

      // const data = [
      // { year: "1991", value: 3 },
      // { year: "1992", value: 4 },
      // { year: "1993", value: 13.5 },
      // { year: "1994", value: 5 },
      // { year: "1995", value: 4.9 },
      // { year: "1996", value: 6 },
      // { year: "1997", value: 7 },
      // { year: "1998", value: 9 },
      // { year: "1999", value: 13 },
      // { year: "2000", value: 23 },
      // { year: "2001", value: 3 },
      // { year: "2002", value: 14 },
      // { year: "2003", value: 3.5 },
      // { year: "2004", value: 5 },
      // { year: "2005", value: 4.9 },
      // { year: "2006", value: 26 },
      // { year: "2007", value: 7 },
      // { year: "2008", value: 9 },
      // { year: "2009", value: 13 }
      // ];
      const data = this.g2data;
      const chart = new G2.Chart({
        container: this.$refs.g2box,
        forceFit: true,
        height: offsetHeight,
        padding: [30, 10, 30, 0]
      });
      chart.source(data);
      chart.scale("value", {
        min: 0
      });
      chart.scale("year", {
        range: [0, 1]
      });
      chart.axis(false);
      chart.tooltip(false);
      chart.line().position("year*value");

      chart.render();
    }
  }
};
</script>　
<style lang="scss" scoped>
@import "@/styles/global.scss";
.g2boxStyle {
  display: flex;
  flex-flow: column;
  justify-content: flex-start;
  align-items: flex-start;
  width: vw(180);
  height: vh(34);
  /*  width: 100%;
  height: 100%; */
  position: absolute;
  z-index: 9999;
  /* margin-top: vh(20); */
}
</style>　



