<template>
  <div class="dispatchPlan">
    <div class="outbox">
      <div class="leftcontentoutbox">
        <left />
      </div>
      <div class="rightmapoutbox">
        <right />
      </div>
    </div>
  </div>
</template>
<script>
import left from "./left/index";
import right from "./right/index";
export default {
  name: "index",
  components: {
    left,
    right
  }
};
</script>
<style lang="scss" scoped>
@import "@/styles/global.scss";
.dispatchPlan {
  display: flex;
  flex-flow: column;
  justify-content: center;
  align-items: center;
  width: vw(1920);
  height: vh(1080);
  .outbox {
    display: flex;
    flex-flow: row nowrap;
    justify-content: flex-start;
    align-items: flex-start;
    width: vw(1880);
    height: vh(1040);
    border: 1px #ccc solid;
    .leftcontentoutbox {
      display: flex;
      flex-flow: row nowrap;
      justify-content: flex-start;
      align-items: flex-start;
      height: vh(1039);
      border-right: 1px #ccc solid;
    }
    .rightmapoutbox {
      display: flex;
      flex-flow: column;
      justify-content: flex-start;
      align-items: flex-start;
      /* width: vw(742); */
      width: 100%;
      height: vh(1040);
    }
  }
}
</style>



