<template>
  <div class="threeoutbox">
    <div class="threeoutbox_title">
      <div class="threeoutbox_title_left">控制点设置</div>
      <div class="threeoutbox_title_rightadd" @click="addfun">新增</div>
    </div>
    <div class="threeoutbox_tableoutbox">
      <div class="tableinbox">
        <el-table
          ref="multipleTable"
          :data="tableData"
          tooltip-effect="dark"
          style="height: 100%"
          @selection-change="handleSelectionChange"
        >
          <el-table-column type="selection" width="55"></el-table-column>
          <el-table-column label="压力控制点名称">
            <template slot-scope="scope">{{ scope.row.pname }}</template>
          </el-table-column>
          <el-table-column prop="lowp" label="最低压力"></el-table-column>
          <el-table-column prop="highp" label="最高压力"></el-table-column>
          <el-table-column prop="targetp" label="目标压力"></el-table-column>
          <el-table-column label="曲线">
            <template slot-scope="scope">
              <div @click="handleSetEcharts(scope.$index, scope.row.ehartsdata)" class="echartsbox">
                <img src="../../../../assets/img/深水_日常调度_切图_44.png" alt />
              </div>
            </template>
          </el-table-column>
          <el-table-column label="结果" show-overflow-tooltip>
            <template slot-scope="scope">
              <div class="jgbox">
                <div
                  class="jgbox_fontbox"
                  :class="scope.row.jgdata==false?'disactive':'active'"
                >{{ scope.row.jgdata==false?"不符合":"符合"}}</div>
              </div>
            </template>
          </el-table-column>
        </el-table>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  name: "threeindex",
  data() {
    return {
      tableData: [
        {
          pname: "压力控制点1",
          lowp: "0.25",
          highp: "0.36",
          targetp: "0.32",
          ehartsdata: "array",
          jgdata: false
        },
        {
          pname: "压力控制点2",
          lowp: "0.25",
          highp: "0.36",
          targetp: "0.32",
          ehartsdata: "array",
          jgdata: true
        },
        {
          pname: "压力控制点3",
          lowp: "0.25",
          highp: "0.36",
          targetp: "0.32",
          ehartsdata: "array",
          jgdata: true
        },
        {
          pname: "压力控制点4",
          lowp: "0.25",
          highp: "0.36",
          targetp: "0.32",
          ehartsdata: "array",
          jgdata: true
        },
        {
          pname: "压力控制点5",
          lowp: "0.25",
          highp: "0.36",
          targetp: "0.32",
          ehartsdata: "array",
          jgdata: false
        },
        {
          pname: "压力控制点6",
          lowp: "0.25",
          highp: "0.36",
          targetp: "0.32",
          ehartsdata: "array",
          jgdata: true
        },
        {
          pname: "压力控制点7",
          lowp: "0.25",
          highp: "0.36",
          targetp: "0.32",
          ehartsdata: "array",
          jgdata: false
        },
        {
          pname: "压力控制点8",
          lowp: "0.25",
          highp: "0.36",
          targetp: "0.32",
          ehartsdata: "array",
          jgdata: true
        },
        {
          pname: "压力控制点9",
          lowp: "0.25",
          highp: "0.36",
          targetp: "0.32",
          ehartsdata: "array",
          jgdata: false
        }
      ],
      multipleSelection: []
    };
  },
  methods: {
    handleSelectionChange(val) {
      this.multipleSelection = val;
    },
    handleSetEcharts(index, row) {
      console.log(index, row);
      alert("Echarts曲线按钮");
    },
    addfun() {
      alert("新增按钮");
    }
  }
};
</script>
<style lang="scss" scoped>
@import "@/styles/global.scss";
.threeoutbox {
  display: flex;
  flex-flow: column;
  justify-content: flex-start;
  align-items: flex-start;
  width: vw(1138);
  height: vh(300);
  .threeoutbox_title {
    display: flex;
    flex-flow: row nowrap;
    justify-content: space-between;
    align-items: center;
    width: vw(1138);
    height: vh(52);
    .threeoutbox_title_left {
      display: flex;
      flex-flow: row nowrap;
      justify-content: flex-start;
      align-items: center;
      margin-left: vw(20);
      font: bold vh(16) "微软雅黑";
      color: #909399;
    }
    .threeoutbox_title_rightadd {
      display: flex;
      flex-flow: row nowrap;
      justify-content: flex-start;
      align-items: center;
      margin-right: vw(20);
      font: normal vh(12) "微软雅黑";
      padding: 5px 15px;
      background-color: #409eff;
      border-radius: 15px;
      color: #fff;
      cursor: pointer;
      &:hover{
        background-color:#66b1ff;
      }
    }
  }
  .threeoutbox_tableoutbox {
    display: flex;
    flex-flow: column;
    justify-content: flex-start;
    align-items: flex-start;
    width: vw(1138);
    height: vh(248);
    .tableinbox {
      display: flex;
      flex-flow: column;
      justify-content: flex-start;
      align-items: flex-start;
      width: vw(1098);
      height: vh(248);
      margin-left: vw(20);
      /deep/ .el-table__body-wrapper {
        overflow-y: auto !important;
        overflow-x: hidden !important;
        position: relative !important;
        height: vh(230) !important;
      }
      /deep/ .el-table th {
        padding: 18px 0 !important;
        text-align: center !important;
      }
      /deep/ .el-table td {
        padding: 8px 0 !important;
        text-align: center !important;
      }
      .echartsbox {
        display: flex;
        flex-flow: column;
        justify-content: center;
        align-items: center;
        width: 100%;
        height: 100%;
        cursor: pointer;
      }
      .jgbox {
        display: flex;
        flex-flow: column;
        justify-content: center;
        align-items: center;
        width: 100%;
        height: 100%;
        .jgbox_fontbox {
          display: flex;
          flex-flow: column;
          justify-content: center;
          align-items: center;
          width: vw(60);
        }
        .disactive {
          color: #fff;
          background-color: red;
        }
        .active {
          color: #fff;
          background-color: rgb(119, 170, 42);
        }
      }
    }
  }
}
</style>



