<template>
  <div class="fouroutbox">
    <div class="fouroutbox_title">阀门设置</div>
    <div class="fouroutbox_tableoutbox">
      <div class="tableinbox">
        <el-table
          :data="tableData"
          style="height: 100%"
          :show-header="false"
          :row-class-name="tableRowClassName"
        >
          <el-table-column prop="fmdata"></el-table-column>
          <el-table-column prop="address"></el-table-column>
          <el-table-column label="操作" width="90">
            <template slot-scope="scope">
              <el-button
                @click.native.prevent="deleteRow(scope.$index, tableData)"
                type="text"
                size="small"
              >删除</el-button>
            </template>
          </el-table-column>
        </el-table>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  name: "fourindex",
  data() {
    return {
      tableData: [
        {
          fmdata: "阀门编号abcd,阀门开度调整",
          address: "阀门位置:上海市普陀区金沙江路 1518 弄"
        },
        {
          fmdata: "阀门编号abcd,阀门开度调整",
          address: "阀门位置:上海市普陀区金沙江路 1517 弄"
        },
        {
          fmdata: "阀门编号abcd,阀门开度调整",
          address: "阀门位置:上海市普陀区金沙江路 1519 弄"
        },
        {
          fmdata: "阀门编号abcd,阀门开度调整",
          address: "阀门位置:上海市普陀区金沙江路 1516 弄"
        }
      ]
    };
  },
  methods: {
    deleteRow(index, rows) {
      rows.splice(index, 1);
    },
    //修改table行背景色
    tableRowClassName({ row, rowIndex }) {
      if (rowIndex % 2 === 0) {
        return "shuang-row";
      } else if (rowIndex % 2 !== 0) {
        return "dan-row";
      }
      return "";
    }
  }
};
</script>
<style>
.el-table .shuang-row {
  background: #ccc;
}

.el-table .dan-row {
  background: #f0f9eb;
}
</style>

<style lang="scss" scoped>
@import "@/styles/global.scss";
.fouroutbox {
  display: flex;
  flex-flow: column;
  justify-content: flex-start;
  align-items: flex-start;
  width: vw(1138);
  height: vh(250);
  .fouroutbox_title {
    display: flex;
    flex-flow: row nowrap;
    justify-content: flex-start;
    align-items: center;
    width: vw(1138);
    height: vh(52);
    margin-left: vw(20);
    font: bold vh(16) "微软雅黑";
    color: #909399;
  }
  .fouroutbox_tableoutbox {
    display: flex;
    flex-flow: column;
    justify-content: flex-start;
    align-items: flex-start;
    width: vw(1138);
    height: vh(198);
    .tableinbox {
      display: flex;
      flex-flow: column;
      justify-content: flex-start;
      align-items: flex-start;
      width: vw(1098);
      height: vh(198);
      margin-left: vw(20);
      background-color: #fff;
      /deep/ .el-table__body-wrapper {
        overflow-y: auto !important;
        overflow-x: hidden !important;
        position: relative !important;
        height: vh(230) !important;
      }

      /deep/ .el-table td {
        padding: 14px 0 !important;
      }
      /deep/ .el-button--text {
        color: #fff;
        padding-left: vw(15);
        padding-right: vw(15);
        background-color: #409eff;
      }
      /deep/ .el-table--enable-row-hover .el-table__body tr:hover > td {
        background-color: #212e3e !important;
        color: #fff;
      }
    }
  }
}
</style>



