<template>
  <div class="fiveoutbox">
    <div class="fiveoutbox_title">{{dispatchTypeNum}}</div>
  </div>
</template>
<script>
import { mapState } from "vuex";
export default {
  name: "fiveindex",
  data() {
    return {};
  },
  computed: {
    ...mapState(["dispatchTypeNum"])
  }
};
</script>
<style lang="scss" scoped>
@import "@/styles/global.scss";
.fiveoutbox {
  display: flex;
  flex-flow: row nowrap;
  justify-content: flex-end;
  align-items: center;
  width: vw(1138);
  height: vh(70);
  .fiveoutbox_title {
    display: flex;
    flex-flow: row nowrap;
    justify-content: center;
    align-items: center;
    font: normal vh(12) "微软雅黑";
    margin-right: vw(20);
    padding: 5px 10px;
    background-color: #409eff;
    border-radius: 5px;
    color: #fff;
    cursor: pointer;
    &:hover {
      background-color: #66b1ff;
    }
  }
}
</style>



