<template>
  <div class="right">
    <div class="webmapstyle">rightmap</div>
    <div class="imgbox">
      <img src="~@/assets/closeimg.png" alt @click="close" v-show="closeopenstatus" />
      <img src="~@/assets/openimg.png" alt @click="open" v-show="!closeopenstatus" />
    </div>
  </div>
</template>
<script>
import { mapState } from "vuex";
export default {
  name: "index",
  computed: {
    ...mapState(["closeopenstatus"])
  },
  methods: {
    close() {
      this.$store.commit("closeimg", false);
    },
    open() {
      this.$store.commit("openimg", true);
    }
  }
};
</script>
<style lang="scss" scoped>
@import "@/styles/global.scss";
.right {
  display: flex;
  flex-flow: column;
  justify-content: flex-start;
  align-items: flex-start;
  width: 100%;
  height: vh(1040);
  padding: 0;
  margin: 0;
  position: relative;
  .webmapstyle {
    position: absolute;
    /*  */
    display: flex;
    flex-flow: column;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: vh(1040);
    pointer-events: auto;
  }
  .imgbox {
    display: flex;
    flex-flow: row nowrap;
    justify-content: flex-start;
    align-items: center;
    margin-left: vw(-5);
    position: relative;
    cursor: pointer;
    img {
      /* width: vw(16); */
      height: vh(28);
      pointer-events: auto;
    }
  }
}
</style>



